//--------------------------------------------------------------------------------------------------------------------------------------------------------------
// {{component_name}}
//--------------------------------------------------------------------------------------------------------------------------------------------------------------

.qld__search{
	&__info{
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;

        &-query{
            width: 100%;
            font-weight: bold; 
            color: var(--QLD-color-light-heading);
    
            @include QLD-media(md) {
                @include QLD-space(font-size, 1.5unit);
            }
    
            &-term{
                font-style: italic;
            }
        }
        &-inner{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            border-bottom: 1px solid var(--QLD-color-light-alt-border);
            align-items: baseline;
        }
        &-summary{
            @include QLD-space(font-size, .875unit);
            @include QLD-space(margin-top, 1unit);
            @include QLD-space(margin-bottom, 1unit);
            @include QLD-media(md) {
                @include QLD-space(font-size, 1unit);
            }
        }
        &-summary-text{
            font-weight: bold;
            color: var(--QLD-color-light-heading);
        }
        &-summary-matching{
            font-weight: bold;
        }
    }
    

    &__sort{
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: end;
        align-items: center;
        margin-left: auto;
        
        &-text{
            font-weight: bold;
            color: var(--QLD-color-light-heading);
            @include QLD-space(padding-right, .75unit);
        }
        select{
            max-width: 200px;
        }
    }

    &__filters{
        @include QLD-space(padding-top, 1.25unit);
        // @include QLD-space(padding-bottom, 1.5unit);
        display: flex;
        flex-direction: column;
        

        @include QLD-media(md) {
            flex-direction: row;
        
        }
        @include QLD-media(lg) {
            @include QLD-space(padding-top, 1.5unit);
        
        }
      

        &-title{
            font-weight: bold;
            display: block;
            white-space: nowrap;
            @include QLD-space(padding-right, 1unit);
            @include QLD-space(padding-bottom, .5unit);
            @include QLD-media(md) {
                @include QLD-space(padding-top, .25unit);
                @include QLD-space(padding-bottom, 0unit);
                align-self: center;
            }
               
        }
        &-list{
            margin: 0;
        }
       

    }
    


    &__results &__result{
        margin: 0;
        @include QLD-space(padding-top, 1.5unit);
        @include QLD-space(padding-bottom, 1.5unit);
        border-bottom: 2px solid var(--QLD-color-light-border);

        &:last-child{
            border: none;
        }

        &-header{
            @include QLD-media(lg) {
                h4{
                    @include QLD-space(padding-bottom, .5unit);
                }
               
            }
        }
        &-inner{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;
            @include QLD-space(padding-bottom, .5unit);

            @include QLD-media(lg) {
                @include QLD-space(padding-bottom, 0unit);
            }
        }
        &-type{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            width: 100%;
            @include QLD-space(font-size, .875unit);

            @include QLD-media(lg) {
                width: auto;
                flex-direction: column;
                @include QLD-space(padding-top, 0unit);
            }

            &-name{
                display: flex;
                flex-direction: row;
                align-items: center;
                color: var(--QLD-color-light-heading);
                @include QLD-space(margin-right, .75unit);

                @include QLD-media(lg) {
                    flex-direction: row-reverse;
                }
            }
            .qld__tag{
                margin: 0;

                @include QLD-media(lg) {
                    @include QLD-space(margin-top, .5unit);
                }
            }

            svg{
                width: 1rem;
                height: 1rem;
                @include QLD-space(margin-right, .5unit);
                color: var(--QLD-color-light-alt-button);

                @include QLD-media(lg) {
                    @include QLD-space(margin-right, 0unit);
                    @include QLD-space(margin-left, .5unit);
                }
            }

        }

        &-breadcrumbs{
            
            @include QLD-space(margin, .25unit 0);

            // @include QLD-media(lg) {
            //     @include QLD-space(margin-top, .25unit);
            // }
            .qld__link-list{
                @include QLD-space(font-size, .875unit);
            }

            li:after{
                @include QLD-space(margin,0 .25unit);
                
            }
        }
        *:not([type="hidden"]) &-summary{
            @include QLD-space(font-size, .875unit);
            @include QLD-space(margin-top, .5unit);
            // @include QLD-space(margin-bottom, .5unit);
            @include QLD-media(lg) {
                @include QLD-space(font-size, 1unit);
            }

            .qld__enhanced-search__result-date{
                color: var(--QLD-color-light-text__muted);
            }
        }
    }

    &__results-list{
        padding: 0;
    }


    &__related{
        background-color: var(--QLD-color-light-background)--shade;
        @include QLD-space(margin-top, 1.5unit);
        @include QLD-space(padding-top, 1.5unit);
        @include QLD-space(padding-bottom, 1.5unit);
        @include QLD-space(padding-left, 2unit);
        @include QLD-space(padding-right, 2unit);
        @include QLD-space(
            border-top,
            solid 0.25unit var(--QLD-color-light-designAccent)
        );

        &-title{
            @include QLD-space(font-size, 1.25unit);
            @include QLD-space(padding-top, 1unit);
            @include QLD-space(padding-bottom, .5unit);
            &:first-letter{
                text-transform: capitalize;
            }
            &-name{

            }
            &-query{
                font-weight: bold;
            }
        }
    }


    &__tips{
        background-color: var(--QLD-color-light-background)--shade;
        @include QLD-space(margin-top, 2unit);
        @include QLD-space(padding-top, 1.5unit);
        @include QLD-space(padding-bottom, 1.5unit);
        @include QLD-space(padding-left, 2unit);
        @include QLD-space(padding-right, 2unit);
        @include QLD-space(
            border-top,
            solid 0.25unit var(--QLD-color-light-designAccent)
        );
        &-text{
            font-weight: bold;
        }
        
    }
    
}

//Search Facets
.qld__search{
    
    .qld__body &-link-list{
        list-style-type: disc;
        @include QLD-space(padding-top, 1unit);
        @include QLD-space(padding-left, 1unit);
        margin: 0;
        li{
            display: list-item;
        }
    }
}
    

.qld__search{
    .twitter-typeahead{
        display: block !important;
        width: 100%;

        @include QLD-media(lg) {
            width: 72%;
        }

        .qld__text-input{
            // height: 100%;
            @include QLD-media(lg) {
                width: 100%;
            }
        }
    }


    .tt-menu{
        width: 100%;
        @include QLD-space(padding, .5unit 1unit 1unit 1unit );
        background-color: var(--QLD-color-light-background);
        @include QLD-space(
            border-bottom,
            0.25unit solid var(--QLD-color-light-designAccent)
        );
        box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.12), 0px 1px 3px rgba(0, 0, 0, 0.1);
    }

    .tt-selectable{
        @include QLD-space(padding, .5unit 0);
    }
}

